import axios, { AxiosInstance } from "axios";

export interface BaseModel {

}

export class BaseService<T extends BaseModel> {
  protected baseUrl: string = "http://localhost:5000";
  protected entityName: string;

  constructor(entityName: string) {
    this.entityName = entityName;
  }

  async fetchAll(): Promise<T[]> {try {
    const response = await axios.get<T[]>(`${this.baseUrl}/${this.entityName}`);
      return response.data;
    } catch (error) {
      console.error(`Failed to fetch ${this.entityName}:`, error);
      throw error;
    }
  }

  async add(item: T): Promise<T> {
    try {
      const response = await axios.post<T>(`${this.baseUrl}/${this.entityName}`, item);
      return response.data;
    } catch (error) {
      console.error(`Failed to add ${this.entityName}:`, error);
      throw error;
    }
  }

  async update(item: T): Promise<T> {
    return item;
  }

  async delete(id: number): Promise<void> {

  }
}

