import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { studentActions } from './store/studentSlice';
import type { RootState, AppDispatch } from './store/store';

function App() {
  const dispatch = useDispatch<AppDispatch>();
  const { items: students, loading, error } = useSelector((state: RootState) => state.students);

  useEffect(() => {
    dispatch(studentActions.fetchAll());
  }, [dispatch]);

  const handleAddStudent = () => {
    dispatch(studentActions.add({ 
          name: "alsdf",
          createdAt: "2025-05-01 00:00:01",
          updatedAt: "2025-05-01 00:00:02",
    }));
  };

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error}</p>;

  return (
    <div>
      <button onClick={handleAddStudent}>Add Student</button>
      <ul>
        {students.map(student => (
          <li key={student._id}>{student.name} ({student.createdAt})</li>
        ))}
      </ul>
    </div>
  );
}

export default App;
