// createEntitySlice.ts
import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import { castDraft } from 'immer';

interface EntityState<T> {
  items: T[];
  loading: boolean;
  error?: string;
}

interface EntityService<T> {
  fetchAll(): Promise<T[]>;
  add(item: T): Promise<T>;
  // optionally update, delete etc.
}

export function createEntitySlice<T>(
  name: string,
  service: EntityService<T>
) {
  const fetchAll = createAsyncThunk(`${name}/fetchAll`, async () => {
    return await service.fetchAll();
  });

  const add = createAsyncThunk(`${name}/add`, async (item: T) => {
    return await service.add(item);
  });

  const initialState: EntityState<T> = {
    items: [],
    loading: false,
    error: undefined,
  };

  const slice = createSlice({
    name,
    initialState,
    reducers: {},
    extraReducers: (builder) => {
      builder
        .addCase(fetchAll.pending, (state) => {
          state.loading = true;
          state.error = undefined;
        })
        .addCase(fetchAll.fulfilled, (state, action: PayloadAction<T[]>) => {
          state.items = castDraft(action.payload);
          state.loading = false;
        })
        .addCase(fetchAll.rejected, (state, action) => {
          state.loading = false;
          state.error = action.error.message;
        })
        .addCase(add.pending, (state) => {
          state.loading = true;
          state.error = undefined;
        })
        .addCase(add.fulfilled, (state, action: PayloadAction<T>) => {
          state.items.push(castDraft(action.payload));
          state.loading = false;
        })
        .addCase(add.rejected, (state, action) => {
          state.loading = false;
          state.error = action.error.message;
        });
    },
  });

  return {
    reducer: slice.reducer,
    actions: { fetchAll, add },
  };
}
